body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-calendar-picker-indicator{
  width:100%;
  opacity: 0;
  position:absolute;
}

:root{
   
  --backgroundMain:#1f1f1f;
  --backgroundSecondary:#353535;
  --headerColor:#ffffffde;
  --headerFaded:#808080;
  --headerhover:white;
  --accent1:#ff7066;
  --accent2:#de4c4a;
  --accentHover:rgb(243, 66, 66);
  --borderlineColor:#474646;


}
html{
  background-color: #1f1f1f;
  background-color: var(--backgroundMain);
}
.App {
  text-align: center;
  background-color: #1f1f1f;
  background-color: var(--backgroundMain)

}

.loader{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto;
  align-self: center;
  margin-top: 20vh;
}


/* Login */
.login{
  display: flex;
  flex-direction: column;
  width:500px;
  margin: auto;
  height: 800px;
  align-content: center;
  justify-content: center;
  color: #ffffffde;
  color: var(--headerColor);
  grid-gap: 30px;
  gap: 30px;
}
.login__banner{
  text-align: left;
  font-size: 32px;
  font-weight: 400;
}
.login__row{
  text-align: left;
  background-color: #353535;
  background-color: var(--backgroundSecondary);
  padding: 5px 10px;
  border-radius: 5px;
  border: #474646 1px solid;
  border: var(--borderlineColor) 1px solid;
}
.login__label{
  font-size: 12px;
  font-weight: 300;
}
.login__input{
  color: #ffffffde;
  color: var(--headerColor);
  margin-top: 5px;
  background: none;
  outline: none;
  border:none;
  font-size: 16px;
}
.login__button{
  background-color: #de4c4a;
  background-color: var(--accent2);
  height:48px;
  border: none;
  color: #ffffffde;
  color: var(--headerColor);
  border-radius: 5px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}
.login__button:hover{
  background-color: rgb(243, 66, 66);
  background-color: var(--accentHover);
}

/* Navigation &View*/
.nav{
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: #282828;
  z-index: 4;
  height: 44px;
  width: 100%;

  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.091)
}
.nav__header{
  font-family: 'Roboto';
  text-transform: capitalize;
  color: #ffffffde;
  color: var(--headerColor);
  font-weight: 300;
}
.nav__Menu{
  position: fixed;
  left:20px;
  outline:none;
  border: none;
  background: none;
  color: #ffffffde;
  color: var(--headerColor);
  font-size: 20px;
  padding: 5px;
  border-radius: 5px;
}
.nav__Menu:hover{
  background-color: #353535;
  background-color: var(--backgroundSecondary);
}
.nav__icon{
  display: flex;
}
.nav__logout{
  color: #ffffffde;
  color: var(--headerColor);
  background: none;
  outline: none;
  border: none;
  position: fixed;
  right:20px;
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 16px;
}
.nav__logout:hover{
  color: white;
  color: var(--headerhover);
}

.view{
  max-height: 200vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  padding: 80px 55px;
}



/* View Selection */
.navModal{
  position:fixed;
  z-index:1;
  background-color: rgba(0, 0, 0, 0.6);
  height: 100vh;
  width:100vw;
}
.viewSelection{
  position: fixed;
  top: 44px;
  left:0;
  width: 300px;
  height: 100%;
  background-color: #242424;
  z-index: 3;
  padding-top:100px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap:10px;
  gap:10px;
  font-weight: 100;
  
}
.viewSelection__Link{
  box-sizing: border-box;
  text-decoration: none;
  color: #ffffffde;
  color: var(--headerColor);
  display: flex;
  align-items: center;
  justify-content:space-between;
  width:240px; 
  font-size: 20px;
  margin-left: 50px;
  border-radius:5px;
  padding: 5px 12px;
}
.viewSelection__num{
  color: #ff7066;
  color: var(--accent1);
}
.viewSelection__Link:hover{
  background-color: #353535;
  background-color: var(--backgroundSecondary);
}




/* Add Task Styling */
.addTask {
  height: 40px;
  padding: 0;
  display: flex;
  width: 200px;
  font-size: 14px;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  color: #808080;
  color: var(--headerFaded);
  font-family: 'Roboto';
  font-weight: 300;
  align-items: center;

}
.addTask:hover{
  color: #de4c4a;
  color: var(--accent2);
  
}
.addTask__plusIcon{
  font-size: 18px;
  font-weight: 100;
  margin-right:10px;
  align-self: center;
  color: #de4c4a;
  color: var(--accent2);
}
.addTask:hover .addTask__plusIcon{
  color: #FFFFFF;
  background-color: #ff7066;
  background-color: var(--accent1);
  border-radius: 50%;
}







/*Modal*/
.modal{
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  height: 100vh;
  width:100vw;
  z-index: 10;
}

.modal__Background{
  position:fixed;
  background-color: rgba(0, 0, 0, 0.6);
  height: 100vh;
  width:100vw;
}
.modal__dialog{
  background-color: #1f1f1f;
  background-color: var(--backgroundMain);
  border-radius: 10px ;
  box-shadow: 0px 0px 16px 9px rgba(0,0,0,0.6);
  overflow: hidden;
  z-index:1;
}
.modal__close{
  width: 100%;
  border-bottom: #474646 1px solid;
  border-bottom: var(--borderlineColor) 1px solid;
  height:48px;
  display: flex;
  align-items: center;
  justify-content: end;
}
.modal__x{
  border: none;
  outline: none;
  background: none;
  color: #ffffffde;
  color: var(--headerColor);
  font-size: 24px;
  font-weight: 100;
  font-family: 'Roboto';
  margin-right: 16px;
  padding: 3px 10px ;
  border-radius: 5px;
  cursor: pointer;
}
.modal__x:hover{
  background-color: #353535;
  background-color: var(--backgroundSecondary);
}

/* New Task Styling*/  
.newTask__container{
  box-sizing: border-box;
  z-index: 1;
  background-color: #1f1f1f;
  background-color: var(--backgroundMain);
  display: flex;
  flex-direction: column;
  width:600px;
  height: 250px;
  padding : 30px 50px;
  border-radius: 15px ;

   
}
.newTask__containerRow1{
  display: flex;
  flex-direction: column;
  border: 1px solid #474646;
  border: 1px solid var(--borderlineColor);
  border-radius: 5px
}
.newTask__containerRow2{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 12px;
}
.newTask__name{
  color: #ffffffde;
  color: var(--headerColor);
  background: none;
  outline:none;
  font-size: 14px;
  border:none;
  border-radius: 5px;
  padding: 10px 10px 0px 10px;
  font-weight: 600;

}
.newTask__description{
  display: flex;
  text-align: left;
  color: #ffffffde;
  color: var(--headerColor);
  background:none;
  border: none;
  outline:none;
  height:70px;
  width: 390px;
  border-radius: 5px;
  padding: 10px;
  font-family: 'Roboto';
}
.newTask__time{
  background-color: #474646;
  border:none;
}

.newTask__date{
  display: hidden;
  color: #ff7066;
  color: var(--accent1);
  margin-left: 5px;
  background: none;
  border: none;
  cursor: pointer;
}
.newTask__date::-webkit-input-placeholder{
  color: #ff7066;
  color: var(--accent1);
}
.newTask__date:-ms-input-placeholder{
  color: #ff7066;
  color: var(--accent1);
}
.newTask__date::placeholder{
  color: #ff7066;
  color: var(--accent1);
}
.newTask__calendar{
  font-size: 14px;
  color: #ff7066;
  color: var(--accent1);
  cursor: pointer;
  max-width: 50px;
}
.newTask__typeLabel{
  right:400px;
  font-size: 20px;
  color: #ffffffde;
  color: var(--headerColor);
  cursor: pointer;
}
.newTask__typeLabel:hover{
  color: #de4c4a;
  color: var(--accent2);
}
.newTask__type{
  display: none;
}
.newTask__type:checked + .bell{
  color: #de4c4a;
  color: var(--accent2);
}

.btngrp{
  display: flex;
  grid-gap: 14px;
  gap: 14px;
}
.newTask__add{
  height:40px;
  width: 80px;
  background-color: #de4c4a;
  background-color: var(--accent2);
  color: aliceblue;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.newTask__add:hover{
  background-color: rgb(243, 66, 66);
  background-color: var(--accentHover);
}
.newTask__cancel{
  height:40px;
  width: 80px;
  background-color: #292929;
  color: aliceblue;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.newTask__cancel:hover{
  background-color: #353535;
  background-color: var(--backgroundSecondary);
}

/* Today View */
.noTasks{
  color: #474646;
  color: var(--borderlineColor);
  font-size: 150px;
  display: flex;
  position: absolute;
  top:25%;
  left:46%;
}
.noTasks__message{
  color: #ffffffde;
  color: var(--headerColor);
  font-size: 50px;
  font-weight: 100;
  display: flex;
  align-self: center;
}
.task__completedCheck{
  margin-top: 8px;
  color: green;
  background: lightgreen;
  border-radius:50%;
  border: 1px green solid;
  font-size: 20px;
}
.today__header{
  color:rgba(255, 255, 255, 0.87);
  text-align: left;
  font-weight: 600;
  font-size: 20px;
  
}
.today__date{
  color: #808080;
  color: var(--headerFaded);
  font-size: 12px;
  font-weight:300;

}

.container{
  display: flex;
  border-bottom: 1px solid #474646;
  border-bottom: 1px solid var(--borderlineColor);
  position: relative;
}
.task{
  color:rgba(255, 255, 255, 0.87);
  height: 72px;
  cursor: pointer;
  margin-left:8px;
  text-align: left;
  width: 100%;

}
.task__name{
  margin-top: 10px;
  font-size: 14px;
  text-transform: capitalize;
  font-weight: 300;
  margin-bottom: 5px;
}
.task__description{
  color: #808080;
  color: var(--headerFaded);
  font-size: 12px;
  font-weight: 300;

}
.task__date{
  color: #ff7066;
  color: var(--accent1);
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  position:absolute ;
  bottom:5px;
  font-size: 12px;
  font-weight: 300;
}
.task__urgent{
  color: #de4c4a;
  color: var(--accent2);
  font-size: 12px;
  font-weight: 300;
  bottom: 5px;
  right: 50px;
  position: absolute;
  right:0;
}




/* Week View */
.dayHeader{
  font-size: 14px;
  font-weight: 600;
  color: #ffffffde;
  color: var(--headerColor);
  text-align: left;
  padding-bottom: 5px;
  border-bottom: 1px solid #474646;
  border-bottom: 1px solid var(--borderlineColor);
  margin: 0;
}





/* Checkbox Styling */

.checkbox__input{
  display: none;
}
.checkbox {
  margin-top: 10px;
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 1px #808080 solid;
  border: 1px var(--headerFaded) solid;
  border-radius: 50%;
  background: #1f1f1f;
  background: var(--backgroundMain);
  box-shadow: black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  
}
.checkbox::after{
  display: inline-block;
  content:'';
  -webkit-mask: url(/to-do-list/static/media/check.a155d92b.svg);
          mask: url(/to-do-list/static/media/check.a155d92b.svg);
  -webkit-mask-size: cover;
          mask-size: cover;
  height: 12px;
  width:12px;
  background-color: #1f1f1f;
  background-color: var(--backgroundMain);
}
.checkbox__input:hover + .checkbox::after {
  background-color: #808080;
  background-color: var(--headerFaded);
}


/* Selected Task */
.selected{ 
  display: flex;
  flex-direction: column;
  max-width: 864px;
  width:90vw;
  height: 90vh;
}
.selected__columns{
  display: flex;
  flex-direction: row;
  height:100%
}
.selected__column1{
  display: flex;
  flex-direction: column;
  width: 70%;
  padding: 20px 20px;

}
.selected__column2{
  display: flex;
  flex-direction: column;
  width: 25%;
  background-color: #242424;
  padding: 50px 20px;

}

.selected__containerRow{
display: flex;
align-items: center;
margin-top: 20px;
position: relative;
}


.selectedCheckbox__input{
  display: none;
}
.selectedCheckbox {
  content: '';
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 2px #474646 solid;
  border-radius: 50%;
  background: #1f1f1f;
  background: var(--backgroundMain);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  
}
.selectedCheckbox::after{
  display: inline-block;
  content:'';
  -webkit-mask: url(/to-do-list/static/media/check.a155d92b.svg);
          mask: url(/to-do-list/static/media/check.a155d92b.svg);
  -webkit-mask-size: cover;
          mask-size: cover;
  height: 10px;
  width:10px;
  background-color: #1f1f1f;
  background-color: var(--backgroundMain);
}
.selectedCheckbox__input:hover + .selectedCheckbox::after {
  background-color: aliceblue;
}
.selected__nameCont{
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: absolute;
  top:-7px;
  left:32px;
  padding: 5px;
  border-radius: 5px;
  height: 90px;
  width: 450px;
  text-align: left;
  border: 1px transparent solid;

}
.selected__descCont{
  display: flex;
  align-items: center;
}
.selected__name{
  border: none;
  background:none;
  outline:none;
  color: #ffffffde;
  color: var(--headerColor);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px
}
.completedName{
  border: none;
  background:none;
  outline:none;
  color: #808080;
  color: var(--headerFaded);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px; text-decoration: line-through;
}
.paraIcon{
  color: #808080;
  color: var(--headerFaded);
}
.selected__description{
  font-weight: 600;
  border: none;
  background:none;
  outline:none;
  color:#ffffffde;
  color:var(--headerColor);
  font-size: 12px;

}
.selected__commentsLine{
  border-bottom: #474646 1px solid;
  margin-top: 90px;
  margin-left:40px;
}
.selected__containerRow--comments{
  margin-top: 30px;
  margin-left:60px;
  border: #474646 solid 1px;
  border-radius: 10px;
  text-align: left;
}

.selected__comments{
  border: none;
  background:none;
  outline:none;
  width: 100%;
  padding: 5px;
  color: #ffffffde;
  color: var(--headerColor);
}
.selected__containerRow--comments:hover{
  background-color: #353535;
  background-color: var(--backgroundSecondary);
}
.selectedCalendar{
  color: rgb(243, 66, 66);
}
.selected__datecont{
  display: flex;
  align-items: center;
}
.selected__date{
  margin-left: 10px;
  border: none;
  background:none;
  outline:none;
  color: #ffffffde;
  color: var(--headerColor);
  font-weight: 300;
  font-family: 'Roboto';
}
.selected__typeLabel{
  margin-left: 10px;
  color: #ffffffde;
  color: var(--headerColor);
  transition: ease 300ms;
  cursor: pointer;
  font-weight: 400;
}
.selected__typeLabel:hover{
  margin-left: 10px;
  color: rgb(243, 66, 66);
}
.selected__type{
  align-self: center;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  vertical-align: middle;
  -webkit-appearance: none;
  background: none;
  background-color: #FFFFFF;
  cursor: pointer;
  transition: ease 300ms;
}
.selected__type:hover{
  align-self: center;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  vertical-align: middle;
  -webkit-appearance: none;
  background: none;
  background-color: rgb(243, 66, 66);
  cursor: pointer;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.selected__type:checked{
  align-self: center;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  vertical-align: middle;
  -webkit-appearance: none;
  background: none;
  background-color: rgb(243, 66, 66);
  background-color: var(--accentHover);
  
  -webkit-transform: scale(1);
  
          transform: scale(1);
}
.selected__save{
  margin: 100px auto;
  height:40px;
  width: 80px;
  background-color: #de4c4a;
  background-color: var(--accent2);
  color: aliceblue;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.selected__save:hover{
  background-color: rgb(243, 66, 66);
  background-color: var(--accentHover);

}
.selected__delete{
  margin: 0 auto;
  height:40px;
  width: 80px;
  background-color: #de4c4a;
  background-color: var(--accent2);
  color: aliceblue;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.selected__delete:hover{
  background-color: rgb(243, 66, 66);
  background-color: var(--accentHover);

}
.selected__column2Line{
  
  border-bottom: #474646 1px solid;
  
  border-bottom: var(--borderlineColor) 1px solid;
  margin-top: 30px;

}

/* Selected Completed */
.selected--completed{
  pointer-events: none;
}
.selected--completed input, .selected--completed label{
  color: #474646;
  color: var(--borderlineColor);
}
.selected--completed .selected__name{
  text-decoration: line-through;
}
.selected--completed .selectedCheckbox{
  background-color: green;
  border-color: lightgreen;
}
.selected--completed .selectedCheckbox::after{
  background-color: lightgreen;
}

/* navModal Transitions */
.nmodal-enter {
  opacity: 0;
  
}
.nmodal-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.nmodal-exit{
  opacity: 1;
}
.nmodal-exit-active{
  opacity: 0;
  transition: opacity 500ms ease-in;
}



/* add/update modal */
.modalTransition-enter{
  opacity: 0;
  
}
.modalTransition-enter-active{
  opacity: 1;
  transition: opacity 100ms ease-in;
}
.modalTransition-exit{
  opacity: 1;
}
.modalTransition-exit-active{
  opacity: 0;
  transition: opacity 250ms ease-in;
}





/* ViewSelection Trannsition */
.nmodal-enter~.viewSelection {
  -webkit-transform:translateX(-100%) ;
          transform:translateX(-100%) ;
  
}
.nmodal-enter-done ~ .viewSelection,.nmodal-enter-active ~ .viewSelection{
  -webkit-transform:translateX(0);
          transform:translateX(0);
  transition: -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
  transition: transform 250ms cubic-bezier(.4,0,.2,1);
  transition: transform 250ms cubic-bezier(.4,0,.2,1), -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
}

.nmodal-exit~ .viewSelection{
  -webkit-transform:translateX(0);
          transform:translateX(0);
}
.nmodal-exit-done ~ .viewSelection,.nmodal-exit-active ~ .viewSelection{
  -webkit-transform:translateX(-100%);
          transform:translateX(-100%);
  transition: -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
  transition: transform 250ms cubic-bezier(.4,0,.2,1);
  transition: transform 250ms cubic-bezier(.4,0,.2,1), -webkit-transform 250ms cubic-bezier(.4,0,.2,1);
}
